.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.main-row {
  width: 100%;
}

body{
  background: #18BC9C; /* For browsers that do not support gradients */
    background: -webkit-gradient(linear, right top, left top, from(rgba(255,0,0,0)), to(#18BC9C));
    background: -webkit-linear-gradient(right, rgba(255,0,0,0), #18BC9C);
    background: -o-linear-gradient(right, rgba(255,0,0,0), #18BC9C);
    background: linear-gradient(to left, rgba(255,0,0,0), #18BC9C);
}


